import { Component, OnInit } from '@angular/core';
import { Subject, Subscription } from 'rxjs';

import { AlertService } from 'src/app/services/alert.service';
import { VehicleModel } from 'src/app/Model/VehicleModel';
import { VehicleService } from "src/app/services/vehiclemodel/vehicle.service";

@Component({
  selector: 'app-view-vehiclemodel',
  templateUrl: './view-vehiclemodel.component.html',
  styleUrls: ['./view-vehiclemodel.component.css']
})
export class ViewVehiclemodelComponent implements OnInit {
  search: string;
  listData: VehicleModel[] = [];
  isvisible = false;
  modalTitle = "Create new Vehicle Model";
  VehicleModelObserverSubject: Subject<VehicleModel> = new Subject();
  contactSubject: Subscription;
  checked = false;
  showVehicleModel = false;
  vehiclemodel = new VehicleModel();
  VehicleModalObserSubject: any;
  constructor(private _vehicleService: VehicleService,
    private _notification: AlertService) { }

  ngOnInit(): void {
    this._vehicleService.vehiclemodalObserve$.subscribe(res => {
      if (res) {
        this.listData = res;
      }
      this.isvisible = false;
    });
    this._vehicleService.GetVehicleModel();
  }
  // ngOnDestroy() {
  //   this.contactSubject.unsubscribe();
  // }
  handleCancel() {
    console.log("aaaa");
    this.isvisible = false;
    this.showVehicleModel = false;
  }
  handleOpen() {
    this.isvisible = true;
    this.modalTitle = "New VehicleModel";
    this.VehicleModelObserverSubject.next(null);
    this.showVehicleModel = false;
  }
  handleEdit(data: VehicleModel) {
    this.modalTitle = "Edit VehicleModel";
    this.isvisible = true;
    this.showVehicleModel = false;
    this.VehicleModalObserSubject.next(data);
  }
  showSingleVehiclemodel(data: VehicleModel) {
    this.vehiclemodel = data;
    this.showVehicleModel = true;
  }
  DeleteVehiclemodel(data: VehicleModel) {
    this._notification.delete().then(result => {
      if (result.isConfirmed) {
        this._vehicleService.DeleteVehiclemodel(data.id).subscribe(res => {
          if (res.isSuccessfull) {
            this._vehicleService.GetVehicleModel();
            this._notification.success('VehicleModel Deleted sucessfully!')
          }
          else{
            this._notification.error('Error while Deleteing VehicleModel!')
          }
        });
      }
      else{
        return;
      }
    });
    this.showVehicleModel=false;
}
}



