import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { BehaviorSubject, Observable } from 'rxjs';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { BaseApiHandlerService } from '../base-api-handler.service';
import { VehicleModel } from "src/app/Model/VehicleModel";


@Injectable({
  providedIn: 'root'
})
export class VehicleService extends BaseApiHandlerService {
  DeleteMotortypes(id: number) {
    throw new Error('Method not implemented.');
  }
  vehiclemodalObserve$:Observable<VehicleModel[]>;
  private VehicleModelSubject$: BehaviorSubject<VehicleModel[]> = new BehaviorSubject<VehicleModel[]>(undefined);

  public get vehiclemodel(){ return this.VehicleModelSubject$.value}

  constructor(private http:HttpClient) {
    super(http);
    this.vehiclemodalObserve$= this.VehicleModelSubject$.asObservable();
  }
  SaveVehicleModel(data){
    return this.Post(data,API_URL+API_ENDPOINTS.VehicleModel);
  }
  UpdateVehicleModel(data:VehicleModel){
    return this.Update(data.id,data,API_URL+API_ENDPOINTS.VehicleModel);
  }
  GetVehicleModel(){
    return this.GetAll(API_URL+API_ENDPOINTS.VehicleModel).subscribe(res =>{
      this.VehicleModelSubject$.next(res.dynamicResult);
    });
  }
  DeleteVehiclemodel(id){
    return this.Delete(id,API_URL+API_ENDPOINTS.VehicleModel);
  }
  }

